<template>
  <div>
    <el-card class="login-form-layout">
        <div style="text-align: center">
           <img :src="LogoSrc" alt="" style="height: 80px;">
        </div>
        <h2 class="login-title color-main">{{strings.title}}</h2>
        <div class="sing-up-step-box">
					<Steps :current="signUpStep">
						<Step :title="strings.step1"  icon="ios-phone-portrait"></Step>
						<Step :title="strings.step2"  icon="ios-person-add"></Step>
						<Step :title="strings.step3" icon="ios-checkmark-circle-outline"></Step>
					</Steps>
					<div class="sign-up-box">
						<transition mode="out-in">
							<router-view></router-view>
						</transition>
					</div>
          <el-button
            style="width: 88%"
            type="primary"
            :loading="loading"
            @click.native.prevent="handleLogin"
          >登录</el-button>
				</div>

    </el-card>
    <img :src="login_center_bg" class="login-center-layout">
  </div>
</template>

<script>
import { isvalidUsername } from "@/utils/validate";
// import {setSupport,getSupport,SupportUrl} from '@/utils/support';
import login_center_bg from "@/assets/images/login_center_bg.png";
	import {mapGetters } from 'vuex';
export default {
  name:"register",
  data(){
    return {
      login_center_bg,
      loading: false,
      status:"",
      strings:{
        title:"注册",
        step1:"验证手机",
        step2:"输入新密码",
        step3:"注册成功",
      },
      LogoSrc:"",
    }
  },
  created(){
   this.status=sessionStorage.getItem("regStatus");
   if(this.status==1){
      this.strings.title="找回密码",
      this.strings.step3="找回成功"
   }
    this.$request.GetSysConfig().then(res=>{
      this.LogoSrc=res.content.HeadQuartersDomain.Value+res.content.Logo.Value;
    })
  },
  methods:{
    handleLogin(){
       this.$router.push({path:"/login"})
    }
  },
  computed: {
			...mapGetters(['signUpStep'])
		},
}
</script>
<style scoped>
.login-form-layout {
  position: absolute;
  left: 0;
  right: 0;
		width: 430px;
  margin: 140px auto;
  border-top: 10px solid #409eff;
}

.login-title {
  text-align: center;
}

.login-center-layout {
  background: #409eff;
  width: auto;
  height: auto;
  max-width: 100%;
  max-height: 100%;
  margin-top: 200px;
}
	.container {
		margin: 15px auto;
		height: 500px;
		overflow: hidden;
	}

	.signup-img-box {
		height: 100%;
		display: flex;
		align-items: center;
		justify-content: center;
	}

	.signup-img-box > img {
		height: 80%;
	}

	.sign-up-title {
		width: 430px;
		margin: 15px auto;
		height: 80px;
	}

	.sing-up-step-box {
		margin: 15px auto;
		padding-left: 30px;
		padding-top: 30px;
		/* width: 430px; */
		height: 350px;
    box-shadow: 0 2px 12px 0 rgba(0,0,0,.1);
	}

	.sign-up-box {
    margin: 30px 0px;
		width: 350px;
		display: flex;
		align-items: center;
	}
</style>



